import React, { useEffect, useState } from 'react';
import './css/HomePage.css';

{/* -- TODO 2 -- */ }
const HomePage = ({ startGameOnClick, mineNumOnChange, boardSizeOnChange, mineNum, boardSize /* -- something more... -- */ }) => {
  const [showPanel, setShowPanel] = useState(false);      // A boolean variable. If true, the controlPanel will show.
  const [error, setError] = useState(false);              // A boolean variable. If true, means that the numbers of mines and the board size are invalid to build a game.

  {/* Some functions may be added here! */ }
  const difficultyAdjustmentOnClick = () => {
    setShowPanel(!showPanel)
  }

  var controlWrapper, color;
  const checkError = () => {
    if (boardSize * boardSize < mineNum) {
      setError(true)
    } else {
      setError(false)
    }
  }

  useEffect(() => {
    checkError() 
  })



  // color
  error ? color = { color: "#880000" } : color = { color: "#0f0f4b" }
  // show Panel
  if (showPanel) {
    controlWrapper =
      <div className="controlWrapper">
        <div className="error" style={color}>{error ? "ERROR: Mines number and board size are invalid!" : ""}</div>
        <div className="controlPanel">
          <div className="controlCol" >
            <p className="controlTitle" >Mines Number</p>
            <input type="range" step="1" min="1" max="50" defaultValue="10" onChange={mineNumOnChange} />
            <p className="controlNum" style={color}>{mineNum}</p>
          </div>
          <div className="controlCol" >
            <p className="controlTitle" >Board Size (nxn)</p>
            <input type="range" step="1" min="." max="20" defaultValue="8" onChange={boardSizeOnChange} />
            <p className="controlNum" style={color}>{boardSize}</p>
          </div>
        </div>
      </div>
  } else {
    controlWrapper = ""
  }


  return (
    <div className='HomeWrapper'>
      <p className='title'>MineSweeper</p>
      {/* -- TODO 1-1 -- */}
      {error ? <button className="btn" >Start Game</button> : <button className="btn" onClick={startGameOnClick}>Start Game</button>}
      <div className="controlContainer" >
        <button className="btn" onClick={difficultyAdjustmentOnClick}>Difficulty Adjustment</button>
        {controlWrapper}
      </div>
      {/* -- TODO 6-2 -- */}
      {/* Useful Hint: <input type = 'range' min = '...' max = '...' defaultValue = '...'> */}
      {/* Useful Hint: Error color: '#880000', default text color: '#0f0f4b', invisible color: 'transparent' */}
      {/* Reminder: The defaultValue of 'mineNum' is 10, and the defaultValue of 'boardSize' is 8. */}

    </div>
  );

}
export default HomePage;